<template>
  <div class="container">
    <Form :model="user" :rules="rules" ref="from" class="login-box">
      <FormItem style="text-align-last: center;">
        <h1>登录</h1>
      </FormItem>
      <FormItem prop="username">
        <Input autofocus prefix="md-person" v-model="user.username" placeholder="用户名" type="text"/>
      </FormItem>
      <FormItem prop="password">
        <Input prefix="md-lock" v-model="user.password" placeholder="密码" type="password"/>
      </FormItem>
      <FormItem prop="captcha" class="captchaBox">
        <Input
          v-model="user.captcha"
          class="captchaInput"
          prefix="md-image"
          type="text"
          placeholder="验证码"
        />
        <Button :loading="captchaLoading" class="captchaBtn">
          <img
            @load="captchaLoading=false"
            @click="changeCaptcha(),captchaLoading=true"
            class="captchaImg"
            :src="captchaUrl"
            alt="验证码"
          >
        </Button>
      </FormItem>
      <FormItem>
        <Button :loading="isLoading" type="primary" @click="login" long>登录</Button>
      </FormItem>
    </Form>
  </div>
</template>

<script>
import uuid from "uuid";
import { createNamespacedHelpers } from "vuex";
const loginMsg = createNamespacedHelpers("loginMsg");
// import { login } from "../../api/login.js";
export default {
  data() {
    return {
      uid: uuid(),
      user: {
        username: "",
        password: "",
        captcha: ""
      },
      captchaLoading: false,
      isLoading: false,
      rules: {
        username: [
          { required: true, message: "用户名必须" },
          { min: 4, max: 18, message: "用户名必须6-18位" }
        ],
        password: [
          { required: true, message: "请输入密码" },
          { min: 2, max: 10, message: "长度必须2-10字符" }
        ],
        captcha: [
          { required: true, message: "请输入验证码" },
          { min: 5, max: 5, message: "必须5位验证码" }
        ]
      }
    };
  },
  methods: {
    changeCaptcha() {
      this.uid = uuid();
    },
    async login() {
      this.isLoading = true;
      const valid = await this.$refs.from.validate();
      this.isLoading = false;
      if (valid) {
        this.isLoading = true;
        const res = await this.USER_LOGIN({ ...this.user, uuid: this.uid });
        const { code, msg, token } = res.data;
        if (code === 0) {
          localStorage.setItem("token", token);
          this.$router.replace("/sys");
          this.isLoading = false;
        } else {
          this.$Message.error(`${msg},请重新输入`);
          this.changeCaptcha();
          this.user.username = "";
          this.user.password = "";
          this.user.captcha = "";
          this.isLoading = false;
        }
      } else {
        this.$Message.error({
          content: "请输入登录信息"
        });
        this.changeCaptcha();
      }

      // this.isLoading = true;
      // this.$refs["from"].validate(valid => {
      //   this.isLoading = false;
      //   if (valid) {
      //     this.isLoading = true;
      //     login({ ...this.user, uuid: this.uid }).then(res => {
      //       const { code, msg, token } = res.data;
      //       if (code === 0) {
      //         localStorage.setItem("token", token);
      //         this.$router.replace("/sys");
      //         this.isLoading = false;
      //       } else {
      //         this.$Message.error(`${msg},请重新输入`);
      //         this.changeCaptcha();
      //         this.user.username = "";
      //         this.user.password = "";
      //         this.user.captcha = "";
      //         this.isLoading = false;
      //       }
      //     });
      //   } else {
      //     this.$Message.error({
      //       content: "请输入登录信息"
      //     });
      //     this.changeCaptcha();
      //   }
      // });
    },
    onKeydown(e) {
      if (e.keyCode === 13) {
        if (this.isLoading) return;
        this.login();
      }
    },
    ...loginMsg.mapActions(["USER_LOGIN"])
  },
  created() {
    document.addEventListener("keydown", this.onKeydown);
  },
  destroyed() {
    document.removeEventListener("keydown", this.onKeydown);
  },
  computed: {
    captchaUrl() {
      return "/api/captcha.jpg?uuid=" + this.uid;
    }
  }
};
</script>

<style lang='less' scoped>
.container {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background: url("../../assets/imgs/bac.jpg");
  background-size: cover;
  .login-box {
    width: 350px;
    // background-color: #fff;
    padding: 15px;
    border: 1px solid #ddd;
    box-shadow: 0 1px 4px rgba(0, 0, 0, 0.3), 0 0 20px rgba(0, 0, 0, 0.1) inset;
    border-radius: 5px;
    .captchaBox {
      display: flex;
      .captchaInput {
        width: 120px;
        margin-right: 66px;
      }
      .captchaBtn {
        height: 32px;
        padding: 0;
        border: 0;
        width: 128px;
        img {
          height: 32px;
          vertical-align: middle;
          cursor: pointer;
        }
      }
    }
  }
}
</style>